html {
  --link-color: #00acdc;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

body {
  margin: 0;
}

.header {
  width: 100%;
}

.nav_item {
  padding: 0;
  list-style: none;
}

.nav_link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav_item:not(:first-child) {
  margin-left: 20px;
}

.nav_link:hover, .is-active {
  color: var(--link-color);
}

.is-active:after {
  content: "";
  background-color: var(--link-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

.nav_list {
  margin: 0;
  padding: 0;
  display: flex;
}

.container {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  margin: 0 auto;
  padding-left: 50px;
  padding-right: 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}
